










@import '../lib/styl/vars.styl'
  .big-field
    display flex
    textarea
      padding 0
      font-size .75em
      font-family $monospace-font
    .field
      raw()
      width 100%
      max-width 100%
      height 3em
      background none
      color $color
      border none
      margin 0 0 2em 0
      &:disabled
        color $txt-color
