






































































@import '../lib/styl/vars.styl'

.chart-container
  display flex
  justify-content space-between
  max-width 100%

.tx-chart
  max-height 100%
  height auto

svg
  overflow visible

.curve path
  stroke brand2
  stroke-width 1

.curve-back
  path
    opacity 0.2
    stroke none
