









































































































@import '../lib/styl/vars.styl'
@import '../lib/styl/mixins.styl'

input.page
  width 3em

.pages
  width 100%
  display flex
  margin 1em
  justify-content space-evenly

ul.page-numbers
  display flex
  flex-flow row nowrap
  color $color
  list-style none

  li
    margin 0 0.25em
    min-width 1em
    flex-centered()

  li.selected
    padding 0.0625em 0.125em
    border-radius $border-radius
    border-color $soft-border

.page-button
  .svg-icon *
    fill $dark
