






























































































































































































































































@import '../lib/styl/vars.styl'
@import '../lib/styl/mixins.styl'

.data-table
  display flex
  flex-flow column nowrap
  justify-content center

.table-ctrls
  display flex
  justify-content flex-end
  margin  1em

.unsortable > .field-title
  color gray

  .icon svg
    fill gray !important

.sort
  flex-centered()

  .field-title
    flex-centered()

  button
    display flex

  div
    display flex

  .icon
    margin 0 0.5em 0 0

  .sort-icon
    margin 0 0.25em
    display flex
    justify-content center
    align-items center
    background $color
    width 1em
    height @width
    border-radius 50%

    svg.svg-icon *
      fill $bg-color
      display flex

sub
  color white

.has-sort
  padding 0 !important
