



























































































@import '../lib/styl/vars.styl'
@import '../lib/styl/media_queries.styl'

.data-item
  min-width 100%
  width 100%

  .items
    min-width 100%
    display flex
    flex-flow row wrap
    padding 0.5em 0em

  .item
    display flex
    flex 1 1 100%
    align-items center
    padding 0.5em 0em
    overflow visible

  .item
    font-size 1em

    .field-icon, .field-title
      margin-right 0.5em

    .field-title
      flex 1
      margin 0 1em 0 2em
      justify-content flex-start
      align-self flex-start

    .data-field
      margin 0 2em 0 0em !important
      flex 5

.field-value
  display inline-flex

.custom
  display flex
  flex-flow column wrap
  margin 0 2em 0 0em !important
  flex 5

.custom-item
  display flex
  flex 1 1 100%
  align-items center
  overflow visible
  padding 0.5em 0em
  max-width 100%

  .field-title
    flex 1
    margin 0 1em 0 2em
    justify-content flex-start

.field-icon, .field-title
  color $color

.field-title
  text-transform capitalize
  font-weight bold

.item-title
  text-transform capitalize
  display inline-flex

  .field-value
    display inline-flex

  div, span
    &::before
      content '\00a0'

.field-title.big-field
  justify-content flex-start
  margin-bottom 0.5em

.field-title.big-field, .big-field.custom
  margin 0 1em 0em 2em

.field-title.data-table
  margin 0 !important
  justify-content center !important
  margin-top 1em !important

@media $media_medium
  .data-item
    .big-field.custom
      min-width 20em
