






































@import '../lib/styl/vars.styl'
@import '../lib/styl/mixins.styl'

.collapsible-container
  display flex
  flex 1 1 100%
  max-width 100%
  flex-flow column wrap
  border $frame-border

  .header
    display flex
    flex-flow row nowrap
    margin 0 1em
    link()
    flex-centered()
    justify-content space-between
    break-word()

    .title
      margin 0.25em

      div
        margin 0 1em 0 0

  .content
    display flex
    flex 1 1 100%

.collapsed
  box-shadow $box-sh
  flex 0

.expanded
  display flex
  border $mid-border
  background none !important

.expand-enter-active, .expand-leave-active
  transition all .25s ease-in
  opacity 1

.expand-enter, .expand-leave-to
  opacity 0
