





































































@import '../lib/styl/vars.styl'
@import '../lib/styl/mixins.styl'

.pending-blocks
  justify-content center
  align-items center

  h4
    padding 0
    margin 0

  .cubes
    .fill
      fill orange

    svg
      stroke brand3
      overflow visible

  box()
  padding-bottom 0.5rem

  .chart-title
    margin-bottom 1em

.miners
  min-width 100%
  display flex
  flex-flow row nowrap
  justify-content space-around

.miner
  flex 1
  position relative
  display flex
  align-items center
  flex-flow column nowrap

  svg
    overflow visible

    .cube
      fill $color
      stroke $dark

  .name
    font-size 0.8em

  .cubes
    display inline-block

  .blocks
    position absolute
    right 1.5em
    text-align center

    span
      badge(1.6em)
      border $border
      background none
