














































































































































@import '../lib/styl/vars.styl'

.contract-details
  .verify
    display block
    margin 1em
    width 100%
    text-align right

  .files
    display flex
    flex-flow row wrap
    position relative
    min-width 100%
    width 100%
    justify-content flex-start

    .sel
      font-weight bold
      border-bottom solid 1px $color

    button
      margin 0 0.5em
      font-weight bold

  .selected-file-enter-active, .selected-file-leave-active
    transition opacity 0.2s ease-in

  .selected-file-enter, .selected-file-leave-to
    opacity 0
