





















































































@import '../lib/styl/vars.styl'
@import '../lib/styl/mixins.styl'

.margin-less
  margin-bottom 0

.search-msg
  flex-centered()
  flex-flow column wrap
  transition all 0.5s ease
  flex 0 1 100%
  opacity 1
  position relative
  margin-bottom -2em

.search
  flex-flow row wrap
  ::placeholder
    color $graylight

  button
    margin 0 0.5rem 0 0
    display inline-block

  #search
    text-align center
    padding 0
    background none
    border-style solid
    font-size .75em

.msg-trans
  will-change opacity

.msgtrans-enter-active
  opacity 0

.msgtrans-leave-to
  transition all 0.5s ease
  transform translateY(-1em)
  opacity 0
