





















































































@import '../lib/styl/vars.styl'
@import '../lib/styl/mixins.styl'
@import '../lib/styl/lists.styl'

.data-field
  ul
    display flex
    flex-flow column
    flex 1
    margin 0
    padding 0

    li
      break-word()

  ul.array
    font-size 0.9em
    list-style none

.data-field, .data-field > a, .data-field > .tooltip, max-width 100%
  display flex
  position relative
  break-word()
  justify-content center
  align-items center

.field-value
  overflow-wrap break-word
  word-wrap break-word
  -ms-word-break break-all
  word-break break-all
  word-break break-word
  -ms-hyphens auto
  -moz-hyphens auto
  -webkit-hyphens auto
  hyphens auto

.flex-table
  & td .data-field
    width 100%

.data-field.items-list ul
  list-style none
  margin 0
  padding 0
  display flex
  flex-flow row wrap

  li
    display flex
    margin 0 0 0 1em

  :first-child
    margin 0
