
























@import '../lib/styl/vars.styl'
@import '../lib/styl/mixins.styl'

.event-call
  margin 0
  display flex
  .index
    font-size .8em

  li.event-name
    font-weight bold
    color $info

  ul
    raw()
    font-size .9em
    list-style none
    align-items flex-end
    font-style italic
    font-weight normal
    margin 0
    padding 0
    flex-flow row wrap
    li
      color gray

  ul,li
    margin 0 0.25em 0 0
    display flex

    &:last-child:after
      font-weight bold

  .args
    &::before
      content '('

    &::after
      content ')'

    .type
      color $info
      &:after
        content ''

    .name
      font-size 0.9em
      color $txt-color

      &:after
        content ','
        font-size 1em

      &:last-child:after
        content none
